import { useHistory, useLocation, } from "react-router-dom";

function BreadCrumb() {
    const { pathname } = useLocation();
    const history = useHistory();

    //  tab  ,     
    const handleClickTab = (link: string) => {
        history.push(link);
    }

    return (
        <ul className="nav nav-pills nav-justified breadcrumb">
            <li className="nav-item">
                <span className={`nav-link ${pathname === "/" ? "active" : ""}`} onClick={() => handleClickTab("/")}></span>
            </li>
            <li className="nav-item">
                <span className={`nav-link ${pathname === "/log" ? "active" : ""}`} onClick={() => handleClickTab("/log")}>  </span>
            </li>
        </ul>
    )
}

export default BreadCrumb;